// –¥–æ–±–∞–≤–ª—è–µ—Ç —Ñ–∏–ª—å–º –≤ —Å–ø–∏—Å–æ–∫ "–ü–æ—Å–º–æ—Ç—Ä–µ—Ç—å –ø–æ–∑–∂–µ"
// —Å–ø–∏—Å–æ–∫ "–ü–æ—Å–º–æ—Ç—Ä–µ—Ç—å –ø–æ–∑–∂–µ" –µ—Å—Ç—å –º–∞—Å—Å–∏–≤ —Å –æ–±—å–µ–∫—Ç–∞–º–∏ —Ñ–∏–ª—å–º–æ–≤
// –ø—Ä–∏–Ω–∏–º–∞–µ—Ç –æ–±—å–µ–∫—Ç —Ñ–∏–ª—å–º–∞
function addToWatched({ id, poster_path, title, genres, release_date, vote_average }) {
  // –µ—Å–ª–∏ –≤ localStorage –Ω–µ—Ç –ø–æ–ª—è  'watched' —Ç–æ —Å–æ–∑–¥–∞–µ–º –µ–≥–æ
  // –∏ –¥–µ–ª–∞–µ–º –≤ –Ω–µ–º –º–∞—Å—Å–∏–≤ —Å  —Ñ–∏–ª—å–º–æ–º
  let genre_ids = [];
  // –≤ –º–∞—Å—Å–∏–≤ genre_ids –¥–æ–±–∞–≤–ª—è–µ–º id —Ñ–∏–ª—å–º–æ–≤
  genres.map(genre => {
    genre_ids.push(genre.id);
  });
  const localMovie = {
    id: id,
    poster_path: poster_path,
    title: title,
    genre_ids: genre_ids,
    release_date: release_date,
    vote_average: vote_average,
    // isRating: movie.isRating,
  };

  if (!localStorage.hasOwnProperty('watched')) {
    localStorage.setItem('watched', JSON.stringify([localMovie]));
  } else {
    // –∏–Ω–∞—á–µ –¥–æ–±–∞–≤–ª—è–µ–º –≤ –º–∞—Å—Å–∏–≤ watched
    if (!isInWatched(id)) {
      let watched = JSON.parse(localStorage.getItem('watched'));
      watched.push(localMovie);
      localStorage.setItem('watched', JSON.stringify(watched));
    }
  }
}
// –ø—Ä–æ–≤–µ—Ä—è–µ—Ç –µ—Å—Ç—å –ª–∏ —Ñ–∏–ª—å–º –≤ —Å–ø–∏—Å–∫–µ "–ü–æ—Å–º–æ—Ç—Ä–µ—Ç—å –ø–æ–∑–∂–µ"
// –ø—Ä–∏–Ω–∏–º–∞–µ—Ç id —Ñ–∏–ª—å–º–∞
function isInWatched(id) {
  if (!localStorage.hasOwnProperty('watched')) {
    return false;
  }
  const watched = JSON.parse(localStorage.getItem('watched'));
  let result = false;
  watched.map(movie => {
    if (movie.id === id) {
      result = true;
    }
  });
  return result;
}
// —É–¥–∞–ª—è–µ—Ç —Ñ–∏–ª—å–º –∏–∑ "–ü–æ—Å–º–æ—Ç—Ä–µ—Ç—å –ø–æ–∑–∂–µ"
// –ø—Ä–∏–Ω–∏–º–∞–µ—Ç id —Ñ–∏–ª—å–º–∞
function removeFromWatched(id) {
  let watched = JSON.parse(localStorage.getItem('watched'));
  if (isInWatched(id)) {
    const index = watched.findIndex(movie => movie.id === id);
    watched.splice(index, 1);
    // –µ—Å–ª–∏ –º–∞—Å—Å–∏–≤ watched –ø—É—Å—Ç–æ–π - —É–¥–∞–ª—è–µ–º –µ–≥–æ
    if (watched.length === 0) {
      localStorage.removeItem('watched');
    } else {
      localStorage.setItem('watched', JSON.stringify(watched));
    }
  }
}

export { addToWatched, isInWatched, removeFromWatched };
