// –¥–æ–±–∞–≤–ª—è–µ—Ç —Ñ–∏–ª—å–º –≤ —Å–ø–∏—Å–æ–∫ "Queue"
// —Å–ø–∏—Å–æ–∫ "Queue" –µ—Å—Ç—å –º–∞—Å—Å–∏–≤ —Å —Å –æ–±—å–µ–∫—Ç–∞–º–∏ —Ñ–∏–ª—å–º–æ–≤
// –ø—Ä–∏–Ω–∏–º–∞–µ—Ç –æ–±—å–µ–∫—Ç —Ñ–∏–ª—å–º–∞
function addToQueue({ id, poster_path, genres, release_date, vote_average, title }) {
  // –µ—Å–ª–∏ –≤ localStorage –Ω–µ—Ç –ø–æ–ª—è  'queue' —Ç–æ —Å–æ–∑–¥–∞–µ–º –µ–≥–æ
  // –∏ –¥–µ–ª–∞–µ–º –≤ –Ω–µ–º –º–∞—Å—Å–∏–≤ —Å  —Ñ–∏–ª—å–º–æ–º
  let genre_ids = [];
  // –≤ –º–∞—Å—Å–∏–≤ genre_ids –¥–æ–±–∞–≤–ª—è–µ–º id —Ñ–∏–ª—å–º–æ–≤
  genres.map(genre => {
    genre_ids.push(genre.id);
  });
  const localMovie = {
    id: id,
    poster_path: poster_path,
    title: title,
    genre_ids: genre_ids,
    release_date: release_date,
    vote_average: vote_average,
    // isRating: movie.isRating,
  };

  if (!localStorage.hasOwnProperty('queue')) {
    localStorage.setItem('queue', JSON.stringify([localMovie]));
  } else {
    // –∏–Ω–∞—á–µ –¥–æ–±–∞–≤–ª—è–µ–º –≤ –º–∞—Å—Å–∏–≤ queue
    if (!isInQueue(id)) {
      let queue = JSON.parse(localStorage.getItem('queue'));
      queue.push(localMovie);
      localStorage.setItem('queue', JSON.stringify(queue));
    }
  }
}
// –ø—Ä–æ–≤–µ—Ä—è–µ—Ç –µ—Å—Ç—å –ª–∏ —Ñ–∏–ª—å–º –≤ —Å–ø–∏—Å–∫–µ "Queue"
// –ø—Ä–∏–Ω–∏–º–∞–µ—Ç id —Ñ–∏–ª—å–º–∞
function isInQueue(id) {
  if (!localStorage.hasOwnProperty('queue')) {
    return false;
  }
  const queue = JSON.parse(localStorage.getItem('queue'));
  let result = false;
  queue.map(movie => {
    if (movie.id === id) {
      result = true;
    }
  });
  return result;
}
// —É–¥–∞–ª—è–µ—Ç —Ñ–∏–ª—å–º –∏–∑ "Queue"
// –ø—Ä–∏–Ω–∏–º–∞–µ—Ç id —Ñ–∏–ª—å–º–∞
function removeFromQueue(id) {
  let queue = JSON.parse(localStorage.getItem('queue'));
  if (isInQueue(id)) {
    const index = queue.findIndex(movie => movie.id === id);
    queue.splice(index, 1);
    // –µ—Å–ª–∏ –º–∞—Å—Å–∏–≤ queue –ø—É—Å—Ç–æ–π - —É–¥–∞–ª—è–µ–º –µ–≥–æ
    if (queue.length === 0) {
      localStorage.removeItem('queue');
    } else {
      localStorage.setItem('queue', JSON.stringify(queue));
    }
  }
}

export { addToQueue, isInQueue, removeFromQueue };
