export default function checkQuery(name) {
  /*
   * –ü—Ä–æ–≤–µ—Ä—è–µ–º —Å—Ç—Ä–æ–∫—É –Ω–∞  –Ω–∞–ª–∏—á–∏–µ —Å–∏–º–≤–æ–ª–æ–≤ –∫—Ä–æ–º–µ –ª–∞—Ç–∏–Ω—Å–∫–∏—Ö –±—É–∫–≤
   * –ï—Å–ª–∏ –≤–≤–µ–¥–µ–Ω—ã —Ç–æ–ª—å–∫–æ –ª–∞—Ç–∏–Ω—Å–∫–∏–µ –±—É–∫–≤—ã –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç true
   */
  let regexp = /^[a-zA-Z0-9]/i;
  return regexp.test(name);
}
