import genres from '../data/genres.json';

//–§—É–Ω–∫—Ü—ñ—ó –ø—Ä–∏–π–º–∞—Ç—å –º–∞—Å–∏–≤ —ñ–Ω–¥–µ–Ω—Ç–∏—Ñ—ñ–∫–∞—Ç–æ—Ä—ñ–≤ –∂—ñ–Ω—Ä—ñ–≤ —Ñ—ñ–ª—å–º—É

//–§—É–Ω–∫—Ü—ñ—è –ø–æ–≤–µ—Ä—Ç–∞—î –º–∞—Å–∏–≤ –æ–±'—î–∫—Ç—ñ–≤ –∂–∞–Ω—Ä—ñ–≤ –∑ –ø–æ–ª—è–º–∏ id, name
function searchGenresInData(genre_ids) {
  return genre_ids.map(id => genres.genres.find(g => g.id === id));
}

//–§—É–Ω–∫—Ü—ñ—è –ø–æ–≤–µ—Ä—Ç–∞—î –º–∞—Å–∏–≤ –∑–Ω–∞—á–µ–Ω—å name –∂–∞–Ω—Ä—ñ–≤
function genresFromIdToName(genres) {
  return genres.map(item => item.name);
}

//–§—É–Ω–∫—Ü—ñ—è –ø–æ–≤–µ—Ä—Ç–∞—î –º–∞—Å–∏–≤ –∑–Ω–∞—á–µ–Ω—å name –∂–∞–Ω—Ä—ñ–≤. –Ø–∫—â–æ –∂–∞–Ω—Ä—ñ–≤ –±—ñ–ª—å—à–µ –Ω—ñ–∂ 2, —Ç–æ –ø–æ–≤–µ—Ä—Ç–∞—î—Ç—å—Å—å
//–º–∞—Å–∏–≤ –∑ 3 –∑–Ω–∞—á–µ–Ω—å, –ø–µ—Ä—à—ñ –¥–≤–∞ –∑–∞–Ω–µ–Ω–Ω—è —Ü–µ –Ω–∞–∑–≤–∏ –∂–∞–Ω—Ä—ñ–≤, —Ç—Ä–µ—Ç—î –∑–Ω–∞—á–µ–Ω–Ω—è Other
function genresOthers(genre_ids) {
  let genresItems = searchGenresInData(genre_ids);
  if (genresItems.length > 2) {
    let arr = genresItems.map(item => item.name).slice(0, 2);
    arr.push('Other');
    return arr;
  }
  return genresItems.map(item => item.name);
}

//–ü—Ä–∏ —ñ–º–ø–æ—Ä—Ç—ñ –æ–±–≤'—è–∑–∫–æ–≤—ñ {}
export { genresFromIdToName, genresOthers };
